<template>
   <nav>
    <div class="nav-wrapper green">
        <div class="container">
            <router-link to="/" class="brand-logo"><i class="material-icons">whatshot</i>Fitness siden</router-link>
            <ul id="nav-mobile" class="right hide-on-med-and-down">
                <li><a :class="trigger" :href="modal" @click="logOut"><i class="material-icons left">person</i>{{log}}</a></li>
                <li><a href="#"><i class="material-icons left">business</i>Blog/Nyheder</a></li>
            </ul>
        </div>
    </div>
  </nav>
</template>

<script>
import db from './firebaseInit';
export default {
  data(){
      return{
          log: 'Log ind',
          trigger: 'modal-trigger',
          modal : '#modal1'
      }
  },
  methods : {
      logOut() {
         if(this.trigger == ""){
             db.auth().signOut();
             this.log ="Log ind";
             this.trigger = "modal-trigger";
             this.modal = "#modal1";
             this.$router.push("/");
         }
      }
  },
  mounted(){
      db.auth().onAuthStateChanged(user => {
            console.log(user);
            console.log(this);
            if(user != null){
               this.log ="Log ud";
               this.trigger = "";
               this.modal = "#";
            }
        });
  }
}
</script>




