<template>
  <div class="container">
      <div class="row">
          <div class="col s6">
            <h5>Fitness kæde</h5>
                <div class="input-field">
                <input id="name" type="text" class="validate">
                <label for="name">Navn</label>
                </div>
       
                <div class="input-field">
                <input id="created_at" type="text" class="validate">
                <label for="created_at">Grundlagt</label>
                </div>
      
                <div class="input-field">
                <input id="Web" type="text" class="validate">
                <label for="Web">Hjemmeside</label>
                </div>
        
                <div class="input-field">
                <input id="phone" type="number" class="validate">
                <label for="phone">Tlf. nummer</label>
                </div>
         
                 <label>
                    <input type="checkbox" />
                    <span>Virtuel fitness</span>
                </label>
       
                 <label style="display:block">
                    <input type="checkbox" />
                    <span>App support</span>
                </label>
                <button class="btn" style="margin-top:20px">Tilføj kæde</button>
          </div>
          <div class="col s6">
            <h5>Fitness klub/center</h5>
                <div class="input-field">
                <input id="club_city" type="text" class="validate">
                <label for="club_city">By</label>
                </div>
         
                <div class="input-field">
                <input id="club_zip" type="number" class="validate">
                <label for="club_zip">Post nr.</label>
                </div>
         
                <div class="input-field">
                <input id="club_address" type="text" class="validate">
                <label for="club_address">Addresse</label>
                </div>
        
                <div class="input-field">
                <input id="mandag" type="text" class="validate">
                <label for="mandag">Mandag</label>
                </div>
       

                <div class="input-field">
                <input id="tirsdag" type="text" class="validate">
                <label for="tirsdag">Tirsdag</label>
                </div>
           

                <div class="input-field">
                <input id="onsdag" type="text" class="validate">
                <label for="onsdag">Onsdag</label>
                </div>
         

                <div class="input-field">
                <input id="torsdag" type="text" class="validate">
                <label for="torsdag">Torsdag</label>
                </div>
          

                <div class="input-field">
                <input id="fredag" type="text" class="validate">
                <label for="fredag">Fredag</label>
                </div>
          

                <div class="input-field">
                <input id="loerdag" type="text" class="validate">
                <label for="loerdag">Lørdag</label>
                </div>
     

                <div class="input-field">
                <input id="soendag" type="text" class="validate">
                <label for="soendag">Søndag</label>
                </div>
    

                <div class="input-field">
                <input id="club_phone" type="text" class="validate">
                <label for="club_phone">Tlf. nummer</label>
                </div>
                <button class="btn">Tilføj center</button>
         
            
            
          </div>
      </div>
    </div>
</template>


<script>
import db from './firebaseInit';

export default {
    beforeRouteEnter(to, from, next) {
        // guard this route
        db.auth().onAuthStateChanged(user =>{
            console.log(user);
            if(user != null){
               next();
            }
        });
        
    },
  
}
</script>



