<template>
  <div class="container" style="margin-top:20px">
      <div class="row">
        <div class="col s3">
            <div class="card">
                <div class="card-image">
                <img src="../assets/card-header.jpg">
                <span class="card-title">Filter</span>
                </div>
                <div class="card-content">
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Suscipit aliquid officiis odit temporibus, quisquam, placeat tenetur maiores unde blanditiis minus eligendi accusantium recusandae, alias vitae magnam harum molestias ad cum. </p>
                    <br>
                    <p>
                        <label>
                            <input type="checkbox" />
                            <span>Døgnåbent</span>
                        </label>
                    </p>
                    <p>
                        <label>
                            <input type="checkbox" />
                            <span>Udendørs faciliteter</span>
                        </label>
                    </p>
                    <p>
                        <label>
                            <input type="checkbox" checked="checked" />
                            <span>Virtuel fitness</span>
                        </label>
                    </p>
                    <p>
                        <label>
                            <input type="checkbox"/>
                            <span>Virtuel fitness app support</span>
                        </label>
                    </p>
                    <div class="input-field">
                        <i class="material-icons prefix">search</i>
                        <input type="text" v-model="search" id="searchfield"/>

                    </div>
                </div>
                <div class="card-action">
                    <a class="waves-effect waves-light btn full green" @click="searchAndFilter"><i class="material-icons right">search</i>Søg</a>
                </div>

            </div>
        </div>
        <div class="col s7">
            <p v-if="searchResult.length === 0">Ingen registrerede fitnesskæder eller centre matcher søgeordet {{search}}.</p>
            <div  v-for="aTest in searchResult" :key="aTest.gym_id">
                <searchResult :gym_id="aTest.id" :msg="aTest.name" :type="aTest.type"></searchResult>
            </div>
        </div>
   
      </div>
  </div>
</template>

<script>
import axios from 'axios';
import db from './firebaseInit';
import noUiSlider from '../../node_modules/materialize-css/extras/noUiSlider/nouislider';
import '../../node_modules/materialize-css/extras/noUiSlider/nouislider.css';
import searchResult from './SearchResult';

export default {
  name: 'Resultpage',
  data() {
      return {
          search : null,
          searchResult : [],
          searchDiv : '',
          chains : [],
          test : []
      };
  },
  components : {
      searchResult
  },
  methods : {
      gymLookUp(){
          return db.firestore().collection("gyms").get();
      },
      clubLookUp(){
          return db.firestore().collection("clubs").get();
      },
      searchAndFilter(){
          this.searchResult.length = 0;
          Promise.all([this.gymLookUp(),this.clubLookUp()]).then(response => {
           const gyms = response[0];
           const clubs = response[1];
           console.log(this.search.toLowerCase());
           gyms.docs.forEach(gym => {
               //match by chain name
               if(gym.data().name.toLowerCase().indexOf(this.search.toLowerCase()) != (-1) ){
                   console.log(gym.data().name.toLowerCase().indexOf(this.search.toLowerCase()));
                   let temp = gym.data();
                   temp['id'] = gym.id;
                   temp['gym_id'] = gym.id;
                   const type = ['fitnesskæde','24h'];
                   temp['type'] = type;
                   this.searchResult.push(temp);
               }
           });
           clubs.docs.forEach(club => {
               // match title
               if(club.data().name.toLowerCase().indexOf(this.search.toLowerCase()) != (-1)){
                   let temp = club.data();
                   temp['id'] = club.data().gym_id;
                   temp['gym_id'] = club.id;
                   temp['type'] = ['center'];
                   this.searchResult.push(temp);
               }
               // match by city
               else if(club.data().city.toLowerCase().indexOf(this.search) != (-1)){
                   let temp = club.data();
                   temp['id'] = club.id;
                   temp['gym_id'] = club.id;
                   temp['type'] = ['center'];
                   this.searchResult.push(club.data());
               }
           });
           console.log(this.searchResult);
       });
      }
  },
  created(){


  },
  mounted() {
       this.search = this.$route.query.result;
       // perform search
       this.searchAndFilter();
      /*
       var slider = document.getElementById('test-slider');
            noUiSlider.create(slider, {
            start: [0, 500],
            connect: true,
            step: 1,
            orientation: 'horizontal', // 'horizontal' or 'vertical'
            range: {
                'min': 0,
                'max': 100
            },
            format: wNumb({
                decimals: 0
            })
        });
        */
  }
}
</script>



