<template>
  <div class="container frontpage-img" style="width:100%;height:1000px">
    <div class="container" style="padding:40px 0px">
      <div class="row">
        <div class="input-field col l6 s12 push-l3 push-s0">
          <i class="material-icons prefix search-icon">search</i>
          <input type="text" id="search" class="autocomplete" v-on:change="handleSearch" autocomplete="off"/>
          <label class="search-label" for="search">Søg efter fitnesskæde eller by</label>
        </div>
      </div>
      <div class="row" style="margin-top:50px">
        <div class="col s6">
           <ul class="collapsible">
            <li>
              <div class="collapsible-header"><i class="material-icons">filter_drama</i>Blog post #1</div>
              <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
            </li>
            <li>
              <div class="collapsible-header"><i class="material-icons">place</i>Blog post #2</div>
              <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
            </li>
            <li>
              <div class="collapsible-header"><i class="material-icons">whatshot</i>Blog post #3</div>
              <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
            </li>
          </ul>
        </div>
      </div>
      <div class="row">
        <div class="col s6">
           <ul class="collapsible">
            <li>
              <div class="collapsible-header"><i class="material-icons">filter_drama</i>First</div>
              <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
            </li>
            <li>
              <div class="collapsible-header"><i class="material-icons">place</i>Second</div>
              <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
            </li>
            <li>
              <div class="collapsible-header"><i class="material-icons">whatshot</i>Third</div>
              <div class="collapsible-body"><span>Lorem ipsum dolor sit amet.</span></div>
            </li>
          </ul>
        </div>  
      </div>
      <div class="fixed-action-btn">
        <router-link to="/search?result=all" class="btn-floating btn-large green tooltipped" data-tooltip="Klik for at vise alle fitnesskæder i Danmark" data-position="top">
          <i class="large material-icons">language</i>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import M from '../../node_modules/materialize-css/dist/js/materialize.min.js';
import axios from 'axios';
import db from './firebaseInit';

export default {
  name: 'Frontpage',
  data () {
    return {
    }
  },
  methods: {
    handleSearch(event){
       this.$router.push("/search?result="+event.target.value);
    },
    getDanishCities(){
       return axios.get("https://dawa.aws.dk/postnumre");
    },
    getGyms(){
      //return axios.get("");
      return db.firestore().collection("gyms").get();
    }
  },
  mounted (){
    // const tooltips = document.querySelector('.tooltipped');
    // M.Tooltip.init(tooltips, null);
     const options = { 
       data: {
        
        },
        limit: 10,
        onAutocomplete : searchString => {
          //console.log("Autocompleted");
          //this.$router.push("/search?result="+searchString);
        }
     };
     const elem = document.querySelector('.autocomplete');
     const instance = M.Autocomplete.init(elem, options);
     const collapsible = document.querySelectorAll('.collapsible');
     collapsible.forEach(collaps =>{
       M.Collapsible.init(collaps, null);
     });

     axios.all([this.getDanishCities(),this.getGyms()]).then(axios.spread(function(cities,gyms){
        console.log(gyms);
        cities.data.forEach(by => {
          options.data[by.navn] = 'https://findicons.com/files/icons/1580/devine_icons_part_2/128/home.png';
        });
        gyms.docs.forEach(gym => {
          options.data[gym.data().name] = 'https://image.flaticon.com/icons/png/128/55/55240.png';
        });
     }));
  },
  beforeDestroy(){
    console.log("unmounted");
  }
}
</script>



