<template>
  <div class="card horizontal">
      <div class="card-image">
          <img src="../assets/repeat.jpg" height=165 />
      </div>
      <div class="card-stacked"><div class="card-content">
        <p>{{msg}}</p>
        <div class="chip right" v-for="aType in type" v-bind:key="aType">
            {{aType}}
        </div>
      </div>
      <div class="card-action">
          <router-link :to="'/gym/'+gym_id">Gå til fitnesskæde</router-link>
      </div>
      </div>
    </div>
</template>


<script>
export default {
    props: ['msg','gym_id','type'],
    data () {
        return {

        }
    }
  
}
</script>



