<template>
  <div class="container">
      <div class="row">
        <div class="carousel">
            <a class="carousel-item" href="#one!"><img src="../assets/repeat.jpg"></a>
            <a class="carousel-item" href="#two!"><img src="../assets/repeat.jpg"></a>
            <a class="carousel-item" href="#three!"><img src="../assets/repeat.jpg"></a>
            <a class="carousel-item" href="#four!"><img src="../assets/repeat.jpg"></a>
            <a class="carousel-item" href="#five!"><img src="../assets/repeat.jpg"></a>
        </div>
      </div>
      <div class="row">
          <div class="col s5">
             <div class="card">
                <div class="card-image">
                    <img src="../assets/fitness_chain.jpg">
                    <span class="card-title">{{chain.name}}</span>   
                </div>
                <div class="card-content">
                     <ul class="collection">
                        <li class="collection-item green white-text">Grunlagt: </li>
                        <li class="collection-item green white-text">Antal centre: {{chain.antal}}</li>
                        <li class="collection-item green white-text">Hjemmeside: </li>
                        <li class="collection-item green white-text">Telefon nr: </li>
                        <li class="collection-item green white-text">Virtuel fitness: <i class="material-icons">check</i></li>
                        <li class="collection-item green white-text">App support: <i class="material-icons">check</i></li>
                     </ul>
                </div>
            </div>
          </div>
          <div class="col s2">
              <ul class="collection with-header">
                <li class="collection-header center tooltipped" data-position="top" data-tooltip="Centre">
                    <i class="material-icons">home</i>
                </li>
                <div v-for="club in clubs" :key="club.id">
                    <Club :name="club.name" :active="club.active" :selectClub="selectClub" :club_id="club.id"> </Club>
                </div>
            </ul>
          </div>
          <div class="col s5">
              <div class="card">
                <div class="card-image">
                    <img src="../assets/fitness_chain.jpg">
                    <span class="card-title">{{selected_club.name}}</span>
                </div>
                <div class="card-content">
                     <ul class="collection">
                        <li class="collection-item green white-text">By: </li>
                        <li class="collection-item green white-text">Addresse: </li>
                        <li class="collection-item green white-text">Åbningstider:
                            <ul>
                                <li>Mandag: </li>
                                <li>Tirsdag: </li>
                                <li>Onsdag: </li>
                                <li>Torsdag: </li>
                                <li>Fredag: </li>
                                <li>Lørdag: </li>
                                <li>Søndag: </li>     
                            </ul> 
                        </li>
                        <li class="collection-item green white-text">Telefon: </li>
                     </ul>
                </div>
            </div>
          </div>
      </div>
      <div class="row">
          <div id="map" style="height: 400px;width: 100%;"></div>
      </div>
  </div>

</template>


<script>
import M from '../../node_modules/materialize-css/dist/js/materialize';
import db from './firebaseInit';
import Club from './Club';

export default {
    data () {
        return {
            id : this.$route.params.id,
            chain : {
                name : null,
                active: '',
                grundlagt: null,
                antal: 0,
                web: null,
                phone: null,
                virtual: null,
                app: null
            },
            clubs: [],
            selected_club : {
                name: null,
            }
        }
    }, 
    components : {
        Club
    },
    methods : {
        selectClub (event){
            const id = event.target.parentNode.attributes.club_id.value;
            //let obj = this.clubs.find(o => o.id === id);
            this.clubs.forEach(club => {
                club.active = '';
                if(club.id == id){
                    this.selected_club = club;
                    club.active = 'active';
                    this.initMap(club.city);
                }
            });
        },
        initMap(address) {
            const geo = new google.maps.Geocoder();
            let request = {
                address : address
              };
              geo.geocode(request,function(result,status){
                console.log(result);
                console.log(status);
                if (status == "INVALID_REQUEST"){
                  M.toast({html: 'No/invalid address for the given club/chain!'}); 
                }
                else {
                  var coords = result[0].geometry.location;
                  var map = new google.maps.Map(document.getElementById('map'), {
                    zoom: 12,
                    center: coords
                  });
                  var marker = new google.maps.Marker({
                    position: coords,
                    map: map
                  });
                }
              });
        }
    },
    mounted(){
        var elem = document.querySelector('.carousel');
        var instance = M.Carousel.init(elem, null);
        console.log(this.id);
        const tooltips = document.querySelector('.tooltipped');
        M.Tooltip.init(tooltips, null);
        db.firestore().collection("clubs").where("gym_id","==",this.id).get().then(querySnapshot => {
            let index = 0;
            querySnapshot.docs.forEach(doc => {
                 const club = {
                      name : doc.data().name,
                      active : (index == 0 ? 'active' : ''),
                      id : doc.id,
                      city : doc.data().city,
                      address : doc.data().address,
                      zip : doc.data().zip
                 };
                 if(index == 0){
                     this.selected_club.name = club.name;
                     this.initMap(club.city+" "+club.zip+", "+club.address);
                 }
                 index++;
                 this.clubs.push(club);
            });
            this.chain.antal = index;
        });
        db.firestore().collection("gyms").doc(this.id).get().then(querySnapshot => { 
           this.chain.name = querySnapshot.data().name;

        });
        
     

    }
}
</script>



