<template>
  <div id="modal1" class="modal">
    <div class="modal-content">
      <h4>Log ind</h4>
       <div class="row">
        <div class="input-field col s6">
          <input id="username" type="text" class="validate">
          <label for="username">Username</label>
        </div>
        <div class="input-field col s6">
          <input id="password" type="password" class="validate">
          <label for="password">Password</label>
        </div>
        <label>{{error}}</label>
      </div>
    </div>
    <div class="modal-footer">
        <button class="btn green" @click="logIn">Log in</button>
        <button class="btn green" @click="closeModal">Cancel</button>
    </div>
  </div>
</template>


<script>
import db from './firebaseInit';

export default {
  data() {
      return {
          instance : null,
          error: ''
      }
  },
  methods : {
      closeModal() {
          this.instance.close();
      },
      logIn() {
          const username = document.getElementById("username").value;
          const password = document.getElementById("password").value;
          const auth = db.auth();
          auth.signInWithEmailAndPassword(username,password).then(response => {
              if(response){
                  this.instance.close();
                  this.$router.push("/admin");
                  
              }
          }).catch(response => {
              console.log(response);
              this.error = response.message;
          });
      }
  },
  mounted(){
     const modal = document.querySelector('.modal');
     this.instance = M.Modal.init(modal, null);
  }
}
</script>



